import template from './dao-input.html';
import './dao-input.scss';

function DaoInputDirective($compile) {
  "ngInject";


  return {
    restrict: 'A',

    template: template,
    replace: true,
    transclude: 'element',
    //compile: DaoInputCompile,
    link: DaoInputLink,
  //controller: DaoInputController,
  //controllerAs: 'vm',
  //bindToController: true,
  };



  function DaoInputCompile(el, attrs, transcludeFn) {
    return DaoInputLink;
  }

  function DaoInputLink(scope, el) {
    var input = el.find('input');
    input.removeAttr('dao-input');
    $compile(input)(scope, (_el, _scope) => {
      input.replaceWith(_el);
    })
  }
}


//function DaoInputController() {
//  var vm = this;
//
//  console.log(vm);
//}

export default DaoInputDirective;



