import './dao.scss';
import './dao-button/dao-button';
import './dao-layout/dao-layout.scss';
import './dao-nav/dao-nav.scss';
import './dao-numeric-badge/dao-numeric-badge.scss';
import './dao-dropdown/dao-dropdown.scss';
import './dao-label/dao-label.scss';

import './dao-svg/svg.js';

import classHelper from './helper/class-helper.js';

import DaoDialog from './dao-dialog/index.js';
import DaoInput from './dao-input/index.js';
import DaoTooltip from './dao-tooltip/index.js';
import DaoPopover from './dao-popover/index.js';
import DaoPanelMultiStep from './dao-panel-multi-steps/index';
import DaoDropdown from './dao-dropdown/index';

var app = angular.module('daoStyle', [
  DaoDialog,
  DaoInput,
  DaoTooltip,
  DaoPopover,
  DaoPanelMultiStep,
  DaoDropdown
]);

var appHelp = classHelper('daoStyle');

import DaoTableDirective from './dao-table/dao-table.directive.js';
import DaoDemoBlockDirective from './dao-demo/dao-demo.directive.js';

app
  .directive('daoTable', DaoTableDirective);

appHelp
  .directive('daoDemoBlock', DaoDemoBlockDirective);

export default 'daoStyle';



